<template>
  <div class="labor-content">
    <time-picker @timeChange="timeChangeHandler"></time-picker>
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <div slot="loading">
        {{loadText}}
      </div>
    <div v-if="!data" class="labor-empty">
      暂无相关数据
    </div> 
    <div v-else>
      <div v-if="data&&Object.keys(data).length" v-for="(companyData, companyName) in data" :key="companyName">
        <div class="company-title" @click="showHandler(companyName,companyData.total)">
          {{companyData.name}}: 
          <div class="company-title-content" v-if='companyData.total'>人力成本：{{toThousands(companyData.total)+"元"}}</div>
          <div class="company-title-content" v-else>暂无人力成本数据</div>
          <i :class="['iconfont','icon-down-circle','company-title-icon',{'icon-rotate':!(active.indexOf(companyName)=== -1)}]" v-if='companyData.total'/>
          <i :class="['iconfont','icon-stop','company-title-icon','company-title-icon-none']" v-else/>
        </div>
        <van-collapse 
        v-model="activeNames" 
        v-if="companyData.result.somme&&Object.keys(companyData.result.somme).length" 
        :class="['labor-cost-padding',{ 'display-none':companyData.total?active.indexOf(companyName)=== -1:true}]">
            <van-collapse-item :border="false" v-for="(value, key) in companyData.result.somme" :key="key" :name="key+companyName">
              <div slot="title" class="labor-title">{{key}}</div>
              <div slot="value" class="labor-title">人力成本：{{toThousands(value)+"元"}}</div>
              <van-cell-group :border="false" v-if="companyData.result.data[key]">
                <van-cell 
                :center="true" 
                :border="false" 
                v-for="(item, index) in companyData.result.data[key]"
                :key="index" 
                :title="item.DEPARTMENT" 
                :value="toThousands(item['SUM(TOTAL+DWYJ+GSHJ)'])+'元'"
                is-link 
                @click="detailHandler(item.DEPARTMENT,companyData['department_cost'])"/>
              </van-cell-group>
              <div class='depatment-empty' v-else>暂无数据</div>
            </van-collapse-item>
        </van-collapse>
        <div class='depatment-empty' v-else>暂无数据</div>
      </div>
    </div>
    </van-pull-refresh>
    <van-popup v-model="show" class="popup-container" position="right" :lazy-render="false">
      <div class="popup">
        <labor-costs-detail ref="detail" @close="popupHandler"></labor-costs-detail>  
      </div>
    </van-popup>
  </div>
</template>

<script>
import { Collapse, CollapseItem, Cell, CellGroup, Toast, PullRefresh, Popup, Icon } from "vant";

import timePicker from "../../components/timePicker";
import laborCostsDetail from './laborCostsDetail';
import { costDetail } from "../../api/laborCosts";

let moment = require("moment");
//字体图标
import '../../iconfont/iconfont.css'

export default {
  components: {
    [Collapse.name]: Collapse,
    [CollapseItem.name]: CollapseItem,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Toast.name]: Toast,
    [PullRefresh.name]: PullRefresh,
    [Popup.name]: Popup,
    [Icon.name]: Icon,
    "time-picker": timePicker,
    "labor-costs-detail":laborCostsDetail
  },
  data() {
    return {
      show:false,//弹出层
      activeNames: [],
      active:[],//展开的项
      data: undefined,
      forecast: false,//是否为预测明年成本
      isLoading:false,//是否下拉刷新
      loadText:"数据获取中...",
      begin: undefined,//开始时间
      end:undefined//结束时间
    };
  },
  methods: {
    //数据千分位及小数处理
    toThousands(num) {
      num=this.forecast?(Number(num)*13).toFixed(0):Number(num).toFixed(0);
      return num.replace(/(\d)(?=(?:\d{3})+$)/g, '$1,');
    },
    //公司展开隐藏操作
    showHandler(companyName,isWork){
      if(isWork){
        if(this.active.indexOf(companyName)===-1){
          this.active.push(companyName)
        }else{
          this.active.splice(this.active.indexOf(companyName),1)
        }
      }
    },
    //将数据传送至部门详细人力成本弹出组件
    detailHandler(department,departmentCost){
      this.popupHandler()
      setTimeout(()=>{this.$refs.detail.handleDetail('open',department,departmentCost[department],this.forecast)},0)
    },
    //弹出层弹出隐藏
    popupHandler(){
      this.show=!this.show
    },
    //下拉刷新
    onRefresh(){
     let obj = {  begin: this.begin, end: this.end };
    this.getData(obj)
    },
    //时间变化数据处理
    timeChangeHandler(begin, end, forecast) {
        this.active=[];
        this.activeNames=[];
        let obj = {  begin: begin, end: end };
        this.getData(obj).then(res => {
          this.begin=begin;
          this.end=end;
          this.forecast = forecast;
        });
    },
    //获取数据
    getData(params) {
      this.isLoading=true;
      return new Promise((resolve, reject) => {
        costDetail(params)
          .then(res => {
            // console.log(res.data)
            this.data = res.data;
            this.loadText="数据获取成功"
            setTimeout(() => {
              this.isLoading = false;
              this.loadText="数据获取中..."
            }, 800);
            resolve(true);
          })
          .catch(err => {
            Toast(err.message || "出错啦，请重试");
            this.loadText="数据获取失败"
            setTimeout(() => {
              this.isLoading = false;
              this.loadText="数据获取中..."
            }, 800);
            reject(err);
          });
      });
    }
  }
};
</script>

<style lang="less">
.labor-content {
  width: 100%;
  height:100%;
  .popup-container{
    width: 100%;
    height:100%;
    .popup{
    height: 100%;
    width: 100%;
    }
  }
  .company-title{
    font-size: .4rem;
    font-weight: 900;
    padding: .3rem;
    color: #333333;
    position: relative;
    .company-title-content{
      float:right;
      padding-right: .7rem;
      font-size: .38rem;
      line-height: .6rem;
      height: .6rem;
    }
    .company-title-icon{
     position: absolute;
     right: .3rem;
     top:.3rem;
     font-size: .5rem;
     color: rgba(0,121,243,.7);
     transition:transform .2s linear;
     -moz-transition: -moz-transform .2s linear; /* Firefox 4 */
     -webkit-transition: -webkit-transform .2s linear; /* Safari 和 Chrome */
     -o-transition: -o-transform .2s linear; /* Opera */
    }
    .icon-rotate{	
      color: rgba(0,121,243,.4);
      transform: rotate(-180deg);
      -ms-transform: rotate(-180deg);		/* IE 9 */
      -webkit-transform: rotate(-180deg);	/* Safari and Chrome */
      -o-transform: rotate(-180deg);		/* Opera */
      -moz-transform: rotate(-180deg);		/* Firefox */
    }
    .company-title-icon-none{
      color: rgba(255, 0, 0, .5)
    }
  }
  .display-none{
    display: none

  }
  .labor-title{ 
    font-weight: 900;
  }
  .van-pull-refresh {
      height: calc(100% - 2.6rem);
      overflow: scroll;
     -webkit-overflow-scrolling: touch;
      .van-pull-refresh__track{
      height:calc(100% - 1.4rem);
      }
    }
  .labor-empty {
    width: 100%;
    font-size: 0.7rem;
    font-weight: 900;
    color: #666666;
    text-align: center;
    padding-top: 2rem;
   }
   .depatment-empty{
    width: 100%;
    font-size: 0.4rem;
    font-weight: 900;
    color: #666666;
    text-align: center;
    padding: .1rem;
   }
   .van-collapse-item__content{
     padding:0 .2rem 0;
   }
   .labor-cost-padding{
     padding: 0 .1rem;
   }
  }
</style>
