<template>
<div class="container">
    <div class="header">
        <van-row type="flex" justify="center" class="row-style" align="center">
            <van-col span="2">开始日期</van-col>
            <van-col span="8">
                <div class="time-content" @click="chooseTime('start')">
                    {{start?start:"请选择开始日期"}}
                    <van-icon name="edit" class="icon"/>
                </div>
            </van-col>
            <van-col span="2">结束日期</van-col>
            <van-col span="8">
                <div class="time-content" @click="chooseTime('end')">
                    {{end?end:"请选择结束日期"}}
                    <van-icon name="edit" class="icon"/>
                </div>
            </van-col>
            <van-col span="3" class="search-icon">
                <van-icon name="search" @click="searchHandler"/>
            </van-col>
        </van-row>
        <van-row type="flex" justify="center"  class="row-style" align="center">
            <van-col span="4" v-for="(item,index) in quickChoose" :key="index">
                <van-button plain :class="{active: index===Choosed, 'col-item': true}" size="small" @click="timeRoundHandler(item,index)">{{item}}</van-button>
            </van-col>
        </van-row>
    </div>
    <div class="footer">
        <van-popup v-model="show" position="bottom">
            <van-datetime-picker
                v-model="currentDate"
                type="year-month"
                :min-date="minDate"
                :max-date="maxDate"
                @confirm="confirmHandler"
                @cancel="cancelHandler"/>
        </van-popup>
    </div>
</div>
    
</template>
<script>
import { Popup, DatetimePicker, Row, Col, Button, Icon } from "vant";
let moment = require("moment");
export default {
  components: {
    [Popup.name]: Popup,
    [DatetimePicker.name]: DatetimePicker,
    [Row.name]: Row,
    [Col.name]: Col,
    [Button.name]: Button,
    [Icon.name]: Icon
  },
  data() {
    return {
      quickChoose:["明年","本年","去年","本季度","上季度","上月"],
      Choosed:0,
      show: false,
      type:undefined,
      startTime: undefined,
      endTime: undefined,
      currentDate: new Date(),
      maxDate:new Date(2028,0,1),
      minDate:new Date(2013,0,1)
      
    };
  },
  mounted(){
      //默认为明年
      this.timeRoundHandler("明年",0)
  },
  computed:{
      start(){
          return this.startTime?moment(this.startTime).format("YYYY-MM"):undefined
      },
      end(){
          return this.endTime?moment(this.endTime).format("YYYY-MM"):undefined
      }
  },
  methods: {
      //时间重置
      reset(){
        this.Choosed=0
        this.show=false,
        this.type=undefined,
        this.startTime=moment(new Date(2013,0,1)).format("YYYY-MM-DD"),
        this.endTime= moment(new Date()).format("YYYY-MM-DD"),
        this.currentDate=new Date(),
        this.maxDate=new Date(2028,0,1),
        this.minDate=new Date(2013,0,1)
      },
      //选择开始结束时间
      chooseTime(type){
          this.show=true
          if(type==="start"){
              this.type="start"
              this.minDate=new Date(2013,0,1);
              this.maxDate=this.endTime?moment(this.endTime).subtract(1, 'months').toDate():new Date(2028,0,1)
              this.currentDate=this.startTime?moment(this.startTime).toDate():new Date();   
          }else{
              this.type="end"
              this.maxDate=new Date(2028,0,1);
              this.minDate=this.startTime?moment(this.startTime).add(1,'months').toDate():new Date(2013,0,1)
              this.currentDate=this.endTime?moment(this.endTime).toDate():new Date();
              
          }
      },
      //确认时间
      confirmHandler(time){
          this.show=false
          this.Choosed=-1
          if(this.type==="start"){
              this.startTime=moment(time).format("YYYY-MM-DD")
          }else{
              this.endTime=moment(time).format("YYYY-MM-DD")
          }
      },
      //取消
      cancelHandler(){
          this.show=false
      },
      //搜索
      searchHandler(){
          this.$emit('timeChange',this.startTime,this.endTime,false)
      },
      //时间范围处理
      timeRoundHandler(time,Choosed){
          this.Choosed=Choosed;
          let m = moment(new Date());
          let year=m.year()
          let quarter=m.quarter()
          let month=m.month()
          let day=m.date()
          let begin=undefined;
          let end=undefined;
          let forecast=false;
          switch(time)
          {
            case "上月":
            begin=moment(new Date(year,month-1,1)).format("YYYY-MM-DD");
            end=moment(new Date(year,month,1)).format("YYYY-MM-DD");
            break;
            case '本季度':
            begin=moment(new Date(year,(quarter-1)*3,1)).format("YYYY-MM-DD");
            end=moment(new Date(year,(quarter*3),1)).format("YYYY-MM-DD");
            break;
            case '上季度':
            begin=moment(new Date(year,(quarter-2)*3,1)).format("YYYY-MM-DD");
            end=moment(new Date(year,(quarter-1)*3,1)).format("YYYY-MM-DD");
            break;
            case "本年":
            begin=moment(new Date(year,0,1)).format("YYYY-MM-DD");
            end=moment(new Date((year+1),0,1)).format("YYYY-MM-DD");
            break;
            case "去年":
            begin=moment(new Date((year-1),0,1)).format("YYYY-MM-DD");
            end=moment(new Date(year,0,1)).format("YYYY-MM-DD");
            break;
            case "明年":
            begin=moment(new Date((year+1),0,1)).format("YYYY-MM-DD");
            end=moment(new Date((year+1),11,31)).format("YYYY-MM-DD");
            forecast=true;
            break;
            }
            if(!forecast){
                this.startTime=begin;
                this.endTime=end;
                this.$emit('timeChange',begin,end,forecast)
            }else{
                this.startTime=begin;
                this.endTime=end;
                begin=moment(new Date(year,month-1,1)).format("YYYY-MM-DD");
                end=moment(new Date(year,month,1)).format("YYYY-MM-DD");
                this.$emit('timeChange',begin,end,forecast)
            }
            
      }

  }
};
</script>
<style lang="less" scoped>
.container {
  font-size: 0.35rem;
  width: 100%;
  .header {
    padding: 0.1rem 0;
    .row-style {
      padding: .2rem .1rem 0 .1rem;
      width:calc(100% - .2rem);
      height: 1rem;
      text-align: center;
      .col-item{
        background-color: rgba(0,121,243,.2);
        border-radius: .1rem;
        border: none;
        box-shadow:0px .1rem .2rem -.1rem rgba(0, 0, 0, .7);
    }
    .active{
          background-color: rgba(0,121,243,.5);
          color: #ffffff;
      }
      .search-icon{
            font-size: .55rem;
            padding-top: .1rem;
            color: #1989FA;
            padding-left: 0.1rem; 
        }
      .time-content {
        border: 1px solid #666666;
        margin: 0 0.15rem;
        padding-left:.2rem; 
        height: 0.9rem;
        line-height: 0.9rem;
        text-align: left;
        position: relative;
        border-radius: .1rem;
        font-weight: 900;
        font-size: .4rem; 
        color: #333333;
        .icon {
          position: absolute;
          right: 0.2rem;
          top: 0.25rem;
        }
      }
    }
  }
}
</style>
