<template>
  <div class="labor-detail-container">
    <header>
      <van-nav-bar :title="department" :fixed="true" class="navbar"></van-nav-bar>
      <van-row type="flex" justify="center" class="labor-row-style" align="center">
            <van-col span="4" class="labor-col-item">姓名</van-col>
            <van-col span="5" class="labor-col-item">应发工资</van-col>
            <van-col span="5" class="labor-col-item">公积金</van-col>
            <van-col span="5" class="labor-col-item">五险</van-col>
            <van-col span="5" class="labor-col-item">公司成本</van-col>
      </van-row>
    </header>
    <div class="labor-detail-content">
        <div v-if="!data" class="labor-detail-empty">暂无相关数据</div>
        <div v-else>
          <van-row
            v-for="(item, index) in data"
            :key="index"
            type="flex"
            justify="center"
            class="labor-row-style"
            align="center"
          >
            <van-col span="4" class="labor-col-item">{{item.NAME}}</van-col>
            <van-col
              span="5"
              class="labor-col-item"
            >{{toThousands(item.TOTAL)}}</van-col>
            <van-col
              span="5"
              class="labor-col-item"
            >{{toThousands(item.DWYJ)}}</van-col>
            <van-col
              span="5"
              class="labor-col-item"
            >{{toThousands(item.GSHJ)}}</van-col>
            <van-col
              span="5"
              class="labor-col-item"
            >{{toThousands(Number(item.TOTAL)+Number(item.DWYJ)+Number(item.GSHJ))}}</van-col>
          </van-row>
        </div>
    </div>
    <footer  @click="handleDetail('close')">
      返回
    </footer>
  </div>
</template>

<script>
import { Row, Col, NavBar } from "vant";

export default {
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [NavBar.name]: NavBar,
  },
  data() {
    return {
      data: undefined,
      department: "",
      forecast:false
    };
  },
  methods: {
    //数据千分位及预测处理（上月数据乘13）
    toThousands(num) {
      num=this.forecast?(Number(num)*13).toFixed(0):Number(num).toFixed(0);
      return num.replace(/(\d)(?=(?:\d{3})+$)/g, '$1,');
    },
    //操作弹窗
    handleDetail(type,department,data,forecast){
      if(type==="open"){
        this.department = department;
        this.data=data;
        this.forecast=forecast;
      }
      if(type==="close"){
        this.$emit('close')
        this.data=undefined;
      }
    },
  }
};
</script>

<style lang="less">
.labor-detail-container{
  width: 100%;
  height:100%;
  font-size: 0.4rem;
  header{
    width: 100%;
    .van-hairline--bottom::after {
    border: none;
    }
    .navbar {
      height: 1.2rem;
      box-shadow: 0px 0.1rem 0.2rem -0.1rem rgba(0, 0, 0, 0.8);
      }
    .labor-row-style {
        padding: 1.5rem 0.1rem 0 0.1rem;}
  }
  footer{
    height: 1.2rem;
    width: 100%;
    font-size: 0.6rem;
    text-align: center;
    line-height: 1.2rem;
    color: #ffffff;
    background:rgba(0,121,243,.5);
  }
.labor-detail-content {
  width: 100%;
  height:calc(100% - 3.7rem);
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  // touch-action: none;
  .labor-detail-empty {
    width: 100%;
    font-size: 0.7rem;
    font-weight: 900;
    color: #666666;
    text-align: center;
    padding-top: 2rem;
  }
}
}
.labor-row-style {
    padding: 0.1rem 0.1rem 0 0.1rem;
    width: calc(100% - 0.2rem);
    height: 1rem;
    text-align: center;
    .labor-col-item {
      background-color: rgba(0, 0, 0, 0.03);
      margin: 0.05rem;
      height: 100%;
      line-height: 1rem;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      width: 100%;
    }
  }
</style>
